.containerHover {
    position: relative;
  }
  
  .imageHover {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }
  
  .middleHover {
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    width:95%;
    height: 40%;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .containerHover:hover .imageHover {
    opacity: 0.3;
  }
  
  .containerHover:hover .middleHover {
    opacity: 1;
  }
  
  .textHover {
    width: 100%;
    height:auto;
    
  }
